import React from 'react';
import { withRouter } from 'react-router-dom';
import { Menu, Input, Label } from 'semantic-ui-react';
import MainMenu from './MainMenu';
// import QuickAdd from './QuickAdd'; // Used for testing
import { useAuth } from 'contexts/AuthContexts';
import { STATUS_OPTIONS } from 'utilities/constants'; 

function HeaderMenu({headerMenuProps, taskListData}) {
    const { logout } = useAuth(); 
    const { handleClick, activeMenuItem, handleOnChange, numOpen, numInProgress, numPending, numResolved, numclosed } = headerMenuProps;


    return (
        <>
            <Menu pointing>
                <Menu.Item
                    name={STATUS_OPTIONS[0]}
                    active={activeMenuItem === STATUS_OPTIONS[0]}
                    onClick={handleClick}
                >
                    Open
                    <Label color='red'>{numOpen}</Label>
                </Menu.Item>
                <Menu.Item
                    name={STATUS_OPTIONS[1]}
                    active={activeMenuItem === STATUS_OPTIONS[1]}
                    onClick={handleClick}
                >
                    In Progress
                    <Label color='blue'>{numInProgress}</Label>
                </Menu.Item>
                <Menu.Item
                    name={STATUS_OPTIONS[2]}
                    active={activeMenuItem === STATUS_OPTIONS[2]}
                    onClick={handleClick}
                >
                    Pending
                    <Label color='yellow'>{numPending}</Label>
                </Menu.Item>
                <Menu.Item
                    name={STATUS_OPTIONS[3]}
                    active={activeMenuItem === STATUS_OPTIONS[3]}
                    onClick={handleClick}
                >
                    Resolved
                    <Label color='green'>{numResolved}</Label>
                </Menu.Item>
                <Menu.Item
                    name={STATUS_OPTIONS[4]}
                    active={activeMenuItem === STATUS_OPTIONS[4]}
                    onClick={handleClick}
                >
                    Closed
                    <Label color='grey'>{numclosed}</Label>
                </Menu.Item>
                <Menu.Menu position='right'>
                    { /* <QuickAdd /> // Use for testing */}
                    <Menu.Item>
                        <Input icon='search' placeholder='Search...' onChange={handleOnChange}/>
                    </Menu.Item>
                    <MainMenu 
                        logout={logout}
                        taskListData={taskListData}
                    />
                </Menu.Menu>
            </Menu>
        </>
    )
}

export default withRouter(HeaderMenu)