import React from 'react';
import ViewItemModal from './ViewItemModal';
import EditItemModal from './EditItemModal';
import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableHead from '@material-ui/core/TableHead';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import { MAIN_TABLE_HEADERS } from 'utilities/constants';

const useStyles = makeStyles({
  table: {
    minWidth: 650,
  },
});

export default function MainTable({ taskListData, activeMenuItem, searchData, comments }) {
    const classes = useStyles();

    function fixDate(seconds) {
        if(seconds) {
            return new Date(seconds * 1000).toLocaleDateString()
        } else {
            return null
        }
    }
    
    if(taskListData) {
        return (
            <TableContainer component={Paper}>
                <Table className={classes.table} aria-label="simple table">
                    <TableHead>
                        <TableRow style={{backgroundColor: "lightgray"}}>
                        {MAIN_TABLE_HEADERS.map((header, index) => {
                            return <TableCell key={index} align="center">{header}</TableCell>
                        })}
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {taskListData.map((row, index1) => {
                            if(
                                (activeMenuItem === row.status && row.title.toLowerCase().includes(searchData)) || 
                                (activeMenuItem === row.status && row.summary.toLowerCase().includes(searchData))) {
                                    const modalFields = {
                                        fbDocumentId: row.id, 
                                        fbIssueType: row.issueType,
                                        fbTitle: row.title,
                                        fbSummary: row.summary,
                                        fbStatus: row.status,
                                        fbStartDate: row.startDate ? new Date(row.startDate.seconds * 1000) : null,
                                        fbDueDate: row.dueDate ? new Date(row.dueDate.seconds * 1000) : null,
                                        fbEstimateHours: row.estimateHours,
                                        fbActualHours: row.actualHours,
                                        fbPatchVersion: row.patchVersion,
                                        fbCategory: row.category,
                                        fbComments: comments
                                    }
                                    return(
                                        <TableRow key={index1}>
                                            <TableCell align="center">
                                                {row.status === 'Closed' ?
                                                    <ViewItemModal
                                                        modalFields={modalFields}
                                                    /> :
                                                    <EditItemModal
                                                        modalFields={modalFields}
                                                />
                                            }
                                            </TableCell>
                                            <TableCell align="center">{row.issueType}</TableCell>
                                            <TableCell align="center">{row.title.substring(0,30)}</TableCell>
                                            <TableCell align="center">{row.summary.substring(0,30) + '...'}</TableCell>
                                            <TableCell align="center">{row.startDate ? fixDate(row.startDate.seconds) : null}</TableCell>
                                            <TableCell align="center">{row.dueDate ? fixDate(row.dueDate.seconds) : null}</TableCell>
                                            <TableCell align="center">{row.estimateHours}</TableCell>
                                            <TableCell align="center">{row.actualHours}</TableCell>
                                            <TableCell align="center">{row.createdDate ? fixDate(row.createdDate.seconds) : null}</TableCell>
                                        </TableRow>
                                    ) 
                            } else {
                                return (null)
                            }
                            
                        })}
                    </TableBody>
                </Table>
            </TableContainer>
        );
    } else {
        return (
            <TableContainer component={Paper}>
                <Table className={classes.table} aria-label="simple table">
                    <TableHead>
                        <TableRow style={{backgroundColor: "lightgray"}}>
                        {MAIN_TABLE_HEADERS.map((header, index) => {
                            return <TableCell key={index} align="center">{header}</TableCell>
                        })}
                        </TableRow>
                    </TableHead>
                </Table>
            </TableContainer>
        )
    }
}