import React, { useState } from 'react';
import { Form, Button } from 'semantic-ui-react';
import { Alert } from 'react-bootstrap';
import { Link, withRouter } from 'react-router-dom';
import { Container } from 'react-bootstrap';
import { useAuth } from 'contexts/AuthContexts';

function Login({ history }) {
    const { login, currentUser } = useAuth();

    const initialState = '';
    const [errorMsg, setErrorMsg] = useState(initialState)
    const [loading, setLoading] = useState(false)
    const [email, setEmail] = useState(initialState)
    const [pwd, setPwd] = useState(initialState)

    async function handleSubmit(e) {
        e.preventDefault();

        try {
            setErrorMsg('')
            setLoading(true)
            await login(email, pwd)
            history.push('/')
        } catch (error) {
            setErrorMsg(error.message)
        }
        console.log(currentUser)
        setLoading(false)
    }

    return (
        <Container className="d-flex align-items-center justify-content-center min-vh-100">
            <div className="w-100 div-main">
                <Form onSubmit={handleSubmit}>
                    <h2 className="text-center mb-4">Login</h2>
                    { errorMsg && <Alert variant='danger'>{errorMsg}</Alert>}
                    <Form.Field>
                        <label>Username</label>
                        <input type="email" placeholder='jsmith@gmail.com' onChange={(e) => {setEmail(e.target.value)}} />
                    </Form.Field>
                    <Form.Field>
                        <label>Password</label>
                        <input  type="password" onChange={(e) => {setPwd(e.target.value)}} />
                    </Form.Field>
                    <div className="w-100 text-center mt-2">
                        <Button className="w-50" type='submit' disabled={loading}>Login</Button>
                    </div>
                </Form>
                <div className="w-100 text-center mt-2">
                    <Link to="/password-reset">Forgot password?</Link>
                </div>
                <div className="w-100 text-center mt-2">
                    Need an Account? <Link to="/sign-up">Sign Up</Link>
                </div>
            </div>
        </Container>
    )
}

export default withRouter(Login)