import React from 'react';
import { withRouter } from 'react-router-dom';
import { Dropdown, Menu } from 'semantic-ui-react';
import { ExportToCsv } from 'export-to-csv'; 
import AddItemModal from './AddItemModal';
import ExportModal from './ExportModal';

function MainMenu({logout, history, taskListData }) {
    function handleOnClick() {
        let newData = []
        taskListData.map(data => {
            newData.push(
                {
                    Status: data.status,
                    Title: data.title,
                    IssueType: data.issueType,
                    Summary: data.summary,
                    PatchVersion: data.patchVersion,
                    Category: data.category,
                    EstimateHours: data.estimateHours,
                    ActualHours: data.actualHours,
                    CreatedDate: new Date(data.createdDate.seconds * 1000).toLocaleDateString(),
                    StartDate: data.startDate ?  new Date(data.startDate.seconds  * 1000).toLocaleDateString() : '',
                    DueDate: data.dueDate ?  new Date(data.dueDate.seconds  * 1000).toLocaleDateString() : '',
                }
            )
        })
        const options = { 
            fieldSeparator: ',',
            quoteStrings: '"',
            decimalSeparator: '.',
            showLabels: true, 
            showTitle: true,
            title: new Date().toLocaleDateString(),
            useTextFile: false,
            useBom: true,
            useKeysAsHeaders: true
          };
        const csvExporter = new ExportToCsv(options)
        csvExporter.generateCsv(newData)
    }

    return (
        <Menu style={{ marginRight: '5px'}}>
            <Dropdown item icon='wrench' simple>
                <Dropdown.Menu>
                <AddItemModal />
                <Dropdown.Item icon='file excel' text='Export' onClick={handleOnClick}></Dropdown.Item>
                {/* <Dropdown.Item icon='user' text='Dashboard' onClick={() => {history.push('/dashboard')}}></Dropdown.Item> */}
                <Dropdown.Divider />
                <Dropdown.Item icon='pen square' text='Edit Profile' onClick={() => {history.push('/update-profile')}}></Dropdown.Item>
                {/* //TODO Create Export feature <Dropdown.Header icon='save' text='Export' onClick={null}></Dropdown.Header> */}
                <Dropdown.Item icon='lock' text='Logout' onClick={logout}></Dropdown.Item>
                </Dropdown.Menu>
            </Dropdown>
        </Menu>
    )
}

export default withRouter(MainMenu)