import React, { useState } from 'react';
import { Button, Modal, Form, Dropdown, Segment, Grid } from 'semantic-ui-react';
import { Alert, Container } from 'react-bootstrap'
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';
import { STATUS_OPTIONS, ISSUE_TYPE_OPTIONS } from 'utilities/constants';
import { useAuth } from 'contexts/AuthContexts';
import CommentCard from './CommentCard';


export default function EditItemModal({ modalFields }) {
  const { 
    fbDocumentId,
    fbIssueType, 
    fbTitle, 
    fbSummary, 
    fbStatus, 
    fbStartDate,
    fbDueDate, 
    fbEstimateHours, 
    fbActualHours, 
    fbPatchVersion,
    fbCategory,
    fbComments
} = modalFields;

  const initialValue = null;
  const { xtUpdateTask } = useAuth()
  const [open, setOpen] = useState(false)
  const [errorMsg, setErrorMsg] = useState(initialValue)
  const [issueType, setIssueType] = useState(fbIssueType)
  const [status, setStatus] = useState(fbStatus)
  const [startDate, setStartDate] = useState(fbStartDate)
  const [dueDate, setDueDate] = useState(fbDueDate)
  const [title, setTitle] = useState(fbTitle)
  const [patchVersion, setPatchVersion] = useState(fbPatchVersion)
  const [category, setCategory] = useState(fbCategory)
  const [summary, setSummary] = useState(fbSummary)
  const [estimateHours, setEstimateHours] = useState(fbEstimateHours)
  const [actualHours, setActualHours] = useState(fbActualHours)



  function handleSubmit(e) {
    e.preventDefault();
    setErrorMsg('')

    try {
      xtUpdateTask(
        fbDocumentId,
        category,
        estimateHours,
        actualHours,
        issueType,
        patchVersion,
        status,
        summary,
        title,
        startDate,
        dueDate
      )
    } catch (error) {
      setErrorMsg(error)
    }
    setOpen(false)
  }

  function getStatusOptions() {
    let statusOptions = []
    STATUS_OPTIONS.forEach(status => statusOptions.push({key: status, text: status, value: status}))
    return statusOptions
  }

  function getIssueTypeOptions() {
    let issueTypeOptions = []
    ISSUE_TYPE_OPTIONS.forEach(issueType => issueTypeOptions.push({key: issueType, text: issueType, value: issueType}))
    return issueTypeOptions
}

  return (
    <Modal
      className='xt-modal'
      onClose={() => setOpen(false)}
      onOpen={() => setOpen(true)}
      open={open}
      trigger={<Button primary size='tiny'>Edit</Button>}
      style={{width: '1400px'}}
    >
      <Segment>
        <Grid columns={2}>
          <Grid.Column>
            <Container className="d-flex align-items-center justify-content-center mt-4">
              <div style={{ width: '700px'}}>
                <Modal.Content>
                  {errorMsg && <Alert variant='danger'>{errorMsg}</Alert>}
                  <Form onSubmit={handleSubmit}>
                      <div className='xt-add-item-modal-flex' style={{justifyContent: 'space-evenly'}}>
                        <Form.Field>
                          <label>Status</label>
                          <Dropdown
                            fluid
                            selection
                            options={getStatusOptions()}
                            style={{width: '200px'}}
                            onChange={(e, data) => {setStatus(data.value)}}
                            defaultValue={fbStatus}
                          />
                        </Form.Field>
                        <Form.Field>
                          <label>Issue Type</label>
                          <Dropdown
                            fluid
                            selection
                            options={getIssueTypeOptions()}
                            style={{width: '200px'}}
                            onChange={(e, data) => {setIssueType(data.value)}}
                            defaultValue={fbIssueType}
                          />
                        </Form.Field>
                        <Form.Field>
                          <label>Start Date</label>
                          <DatePicker selected={fbStartDate} onChange={date => setStartDate(date)} />
                        </Form.Field>
                        <Form.Field>
                          <label>Due Date</label>
                          <DatePicker selected={fbDueDate} onChange={date=> setDueDate(date)} />
                        </Form.Field>
                      </div>
                      <Form.Field>
                          <label>Title</label>
                          <input type="text" onChange={(e) => {setTitle(e.target.value)}} defaultValue={fbTitle}/>
                      </Form.Field>
                      <Form.Field>
                          <label>Summary</label>
                          <input type="text" onChange={(e) => {setSummary(e.target.value)}} defaultValue={fbSummary}/>
                      </Form.Field>
                      <div className='xt-add-item-modal-flex' style={{justifyContent: 'left'}}>
                        <Form.Field>
                            <label>Category</label>
                            <input 
                              type="text" 
                              onChange={(e) => {setCategory(e.target.value)}}
                              style={{width: '175px'}}
                              defaultValue={fbCategory}
                            />
                        </Form.Field>
                        <Form.Field style={{paddingLeft: '20px'}}>
                            <label>Estimated Hours</label>
                            <input 
                              type="text" 
                              onChange={(e) => {setEstimateHours(e.target.value)}}
                              style={{width: '100px'}}
                              value={fbEstimateHours}
                            />
                        </Form.Field>
                        <Form.Field style={{paddingLeft: '20px'}}>
                            <label>Actual Hours</label>
                            <input 
                              type="text" 
                              onChange={(e) => {setActualHours(e.target.value)}}
                              style={{width: '100px'}}
                              defaultValue={fbActualHours}
                            />
                        </Form.Field>
                        <Form.Field style={{paddingLeft: '20px'}}>
                          <label>Patch Version</label>
                          <input 
                            placeholder='1.0.0'
                            type="text" 
                            onChange={(e) => {setPatchVersion(e.target.value)}}
                            style={{width: '200px'}}
                            defaultValue={fbPatchVersion}
                          />
                        </Form.Field>
                      </div>
                      <div className="w-100 text-center mt-2">
                      </div>
                  </Form>
                </Modal.Content>
                <Modal.Actions>
                  <Button color='red' onClick={() => setOpen(false)}>
                    Cancel
                  </Button>
                  <Button
                    content="Update Item"
                    onClick={handleSubmit}
                    positive
                  />
                </Modal.Actions>
              </div>
            </Container>
          </Grid.Column>
          <Grid.Column>
            <CommentCard 
              docId={fbDocumentId}
              comments={fbComments}
            />
          </Grid.Column>
        </Grid>
      </Segment>
    </Modal>
  )
}
