import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { xtFirestore } from '../firebase';
import 'css/xtracker.css';
import HeaderMenu from './HeaderMenu';
import Footer from './Footer';
import MainTable from './MainTable';
import { FIRESTORE_TASK_COLLECTION, FIRESTORE_USER_COLLECTION, FIRESTORE_COMMENT_COLLECTION } from 'utilities/constants';

class XTracker extends Component {
    constructor() {
        super();
        this.state = {
            taskList: null,
            users: null,
            comments: null,
            activeMenuItem: 'Open',
            search: '',
            taskNumOpen: 0,
            taskNumInProgress: 0,
            taskNumPending: 0,
            taskNumResolved: 0,
            taskNumClosed: 0,
            hasRan: false
        }
    }

    handleOnClick = (e, {name}) => {
        this.setState({ activeMenuItem: name })
    }

    handleOnChange = (e) => {
        this.setState({ search: e.target.value.toLowerCase()})
    }

    componentDidMount() {
        xtFirestore
            .collection(FIRESTORE_TASK_COLLECTION)
            .onSnapshot(serverUpdate => {
                const taskList = serverUpdate.docs.map(task => {
                    const data = task.data();
                    data['id'] = task.id;
                    return data;
                })
                this.setState({ taskList: taskList })
            })
            // TODO Add additional user information - Later feature
            // xtFirestore
            // .collection(FIRESTORE_USER_COLLECTION)
            // .onSnapshot(serverUpdate => {
            //     const userList = serverUpdate.docs.map(user => {
            //         const data = user.data();
            //         data['id'] = user.id;
            //         return data;
            //     })
            //     this.setState({ users: userList })
            // })
            xtFirestore
            .collection(FIRESTORE_COMMENT_COLLECTION)
            .onSnapshot(serverUpdate => {
                const commentList = serverUpdate.docs.map(comment => {
                    const data = comment.data();
                    data['id'] = comment.id;
                    return data;
                })
                this.setState({ comments: commentList })
            })
    }

    setTaskNumbers = () => {
        var open = 0
        var inProgress = 0
        var pending= 0
        var resolved = 0
        var closed = 0
        this.state.taskList.map(task => {
            switch(task.status) {
                case 'Open':
                   open++;
                   break;
                case 'In Progress':
                   inProgress++;
                   break;
                case 'Pending':
                   pending++;
                   break;
                case 'Resolved':
                   resolved++;
                   break;
                case 'Closed':
                   closed++;
                   break;
                default:
                    break;
            }
            return false
        })
        this.setState({ 
            taskNumOpen: open,
            taskNumInProgress: inProgress,
            taskNumPending: pending,
            taskNumResolved: resolved,
            taskNumClosed: closed
        })
        this.setState({ hasRan: true })
    }

    render() {
        if(this.state.taskList && this.state.hasRan === false) {
           this.setTaskNumbers()
        }
        const headerMenuProps = {
            activeMenuItem: this.state.activeMenuItem,
            handleClick: this.handleOnClick,
            handleOnChange: this.handleOnChange,
            numOpen: this.state.taskNumOpen,
            numInProgress: this.state.taskNumInProgress,
            numPending: this.state.taskNumPending,
            numResolved: this.state.taskNumResolved,
            numclosed: this.state.taskNumClosed
        }
        console.log(this.state.taskList)

        return (
            <div className="xt-main-container">
                <header className="xt-top">
                    <section className="xt-grid-header">
                        <div>
                            X-TRACKER
                        </div>
                    </section>
                    <section className="xt-grid-header-menu">
                        <HeaderMenu 
                            headerMenuProps={headerMenuProps}
                            taskListData={this.state.taskList}
                        />
                    </section>
                </header>
                <aside className="xt-grid-main xt-table-view">
                    <MainTable 
                        taskListData={this.state.taskList}
                        activeMenuItem={this.state.activeMenuItem}
                        searchData={this.state.search}
                        comments={this.state.comments}
                    />
                </aside>
                <footer className="xt-grid-footer">
                    <Footer />
                </footer>
            </div>
        )
    }
}

export default withRouter(XTracker)