import React, { useState } from 'react';
import { Form, Button } from 'semantic-ui-react';
import { Alert } from 'react-bootstrap';
import { withRouter } from 'react-router-dom';
import { Container } from 'react-bootstrap';
import { useAuth } from 'contexts/AuthContexts';

function UpdateProfile({ history }) {
    const initialState = '';
    const [errorMsg, setErrorMsg] = useState(initialState)
    const [successMsg, setSuccessMsg] = useState(initialState)
    const [displayName, setDisplayName] = useState(initialState)
    const [email, setEmail] = useState(initialState)
    const [pwd, setPwd] = useState(initialState)
    const [pwdConf, setPwdConf] = useState(initialState)
   
    const { updateEmail, updatePassword, updateDisplayName, currentUser } = useAuth();

    function handleSubmit(e) {
        e.preventDefault();
        setErrorMsg('')
        setSuccessMsg('')

        if(pwd.length < 6) {
            return setErrorMsg('Password length must be greater than 6 characters')
        }

        if(pwd !== pwdConf){
            return setErrorMsg('Passwords do not match')
        }

        let promises = []
        if(email !== currentUser.email) {
            promises.push(updateEmail(email))
        }
        if(pwd) {
            promises.push(updatePassword(pwd))
        }
        if(displayName !== currentUser.displayName) {
            promises.push(updateDisplayName(displayName))
        }

        Promise.all(promises)
            .then(() => {
                setSuccessMsg('Updated Successfully')})
            .catch(error => {
                setErrorMsg(error.message)})
    }

    return (
        <Container className="d-flex align-items-center justify-content-center min-vh-100">
            <div className="w-100 div-main">
                <Form onSubmit={handleSubmit}>
                    <h2 className="text-center mb-4">Update Profile</h2>
                    { errorMsg && <Alert variant='danger'>{errorMsg}</Alert>}
                    { successMsg && <Alert variant='success'>{successMsg}</Alert>}
                    <Form.Field>
                        <label>Display Name</label>
                        <input type="text" defaultValue={currentUser.displayName} onChange={(e) => {setDisplayName(e.target.value)}} />
                    </Form.Field>
                    <Form.Field>
                        <label>Username</label>
                        <input type="email" defaultValue={currentUser.email} onChange={(e) => {setEmail(e.target.value)}} />
                    </Form.Field>
                    <Form.Field>
                        <label>Password</label>
                        <input  type="password" onChange={(e) => {setPwd(e.target.value)}} />
                    </Form.Field>
                    <Form.Field>
                        <label>ConfirmPassword</label>
                        <input  type="password" onChange={(e) => {setPwdConf(e.target.value)}} />
                    </Form.Field>
                    <div className="w-100 text-center mt-2">
                        <Button className="w-50" type='submit'>Update</Button>
                    <div className="w-100 text-center mt-2">
                        <Button className="w-50" onClick={() => { history.push('/')}}>Cancel</Button>
                    </div>
                    </div>
                </Form>
            </div>
        </Container>
    )
}

export default withRouter(UpdateProfile)