import React, { useState } from 'react';
import { Button, Comment, Form, Header, Icon } from 'semantic-ui-react';
import { Alert } from 'react-bootstrap';
import { useAuth } from 'contexts/AuthContexts';

export default function CommentCard({ comments, docId, status }) {
    const { currentUser, xtAddComment } = useAuth()
    const initialValue = null
    const [text, setText] = useState(initialValue)
    const [userName, setUserName] = useState(initialValue)

    function handleOnClick(e) {
        e.preventDefault()

        if(text === '' ) {
            return null
        }

        if(userName === '' ) {
            return null
        }

        try{
            xtAddComment(
                docId,
                text,
                currentUser.email,
                currentUser.name ? currentUser.name : userName
            )
        } catch (error) {
            console.log(error)
        }
    }

    return (
        <Comment.Group>
            <Header as='h3' dividing>
                Comments
            </Header>

            <div style={{ overflowY: 'auto', height: '250px'}}>
                {comments.map(comment => {
                    console.log('TaskID: ', comment.taskId)
                    if(docId === comment.taskId) {
                        return (
                            <Comment>
                                <Comment.Avatar src='https://icon-library.com/images/generic-person-icon/generic-person-icon-9.jpg' />
                                <Comment.Content>
                                    <Comment.Author as='a'>Name: {comment.userName} </Comment.Author>
                                    <br />
                                    <Comment.Metadata>
                                        <div>{comment.userEmail} {comment.addedDate ? new Date(comment.addedDate.seconds * 1000).toLocaleString : null}</div>
                                    </Comment.Metadata>
                                    <Comment.Text>{comment.text}</Comment.Text>
                                </Comment.Content>
                            </Comment>
                        )
                    } else {
                        return null
                    }
                })}
            </div>

            {
                status === 'Closed' ?
                    null :
                    <Form reply>
                        <Form.Field>
                            {currentUser.name && <input value={currentUser.name} style={{ width: '350px' }}/>}
                            {!currentUser.name && 
                                <Alert variant='warning' style={{ width: '380px'}}>
                                    <input 
                                        placeholder='Update username in Profile...' 
                                        onChange={(e) => {setUserName(e.target.value)}}
                                        required 
                                        style={{ width: '300px'}}
                                    />
                                    <Icon name='warning sign' size='large' style={{ position: 'absolute', right: '15px', top: '20px'}} />
                                </Alert>
                            }
                        </Form.Field>
                        <Form.Field>
                            {/* <input placeholder='Submit updates here...' ref={textRef} onChange={handleOnChange} style={{height: '200px'}}/>  */}
                            <textarea 
                                onChange={(e) => {setText(e.target.value)}}
                                placeholder='Type updates here...' 
                                rows='5' 
                                cols='50'
                                reuqired
                            /> 
                        </Form.Field>
                        <Button content='Add Reply' labelPosition='left' icon='edit' primary onClick={handleOnClick} />
                    </Form>
            }
        </Comment.Group>
    )
}