import React, { useState } from 'react';
import { Form, Button } from 'semantic-ui-react';
import { Alert } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import { Container } from 'react-bootstrap';
import { useAuth } from 'contexts/AuthContexts';

export default function Signup() {
    const { signup } = useAuth();

    const initialState = '';
    const [errorMsg, setErrorMsg] = useState(initialState)
    const [successMsg, setSuccessMsg] = useState(initialState)
    const [loading, setLoading] = useState(false)
    const [email, setEmail] = useState(initialState)
    const [pwd, setPwd] = useState(initialState)
    const [pwdConf, setPwdConf] = useState(initialState)

    async function handleSubmit(e) {
        e.preventDefault();

        if(pwd.length < 6) {
            return setErrorMsg('Password length must be greater than 6 characters')
        }
        if(pwd !== pwdConf) {
            return setErrorMsg('Password do not match!')
        }

        try {
            setErrorMsg('')
            setLoading(true)
            await signup(email, pwd)
            setSuccessMsg('Registration Complete! You may now view the ')
        } catch {
            setErrorMsg('Error has occured, please contact support!')
        } finally {
            setLoading(false)
        }
    }

    return (
        <Container className="d-flex align-items-center justify-content-center min-vh-100">
            <div className="w-100 div-main">
                <Form onSubmit={handleSubmit}>
                    <h2 className="text-center mb-4">SignUp!</h2>
                    { errorMsg && <Alert variant='danger'>{errorMsg}</Alert>}
                    { successMsg && <Alert variant='success'>{successMsg}<Link to='/'>Dashboard</Link></Alert>}
                    <Form.Field>
                        <label>Username</label>
                        <input type="email" placeholder='jsmith@gmail.com' onChange={(e) => {setEmail(e.target.value)}} rquired/>
                    </Form.Field>
                    <Form.Field>
                        <label>Password</label>
                        <input  type="password" onChange={(e) => {setPwd(e.target.value)}} required/>
                    </Form.Field>
                    <Form.Field>
                        <label>Confirm Password</label>
                        <input  type="password" onChange={(e) => {setPwdConf(e.target.value)}} required/>
                    </Form.Field>
                    <div className="w-100 text-center mt-2">
                        <Button className="w-50" type='submit' disabled={loading}>Submit</Button>
                    </div>
                </Form>
                <div className="w-100 text-center mt-2">
                    Already have an Account? <Link to="/login">Login</Link>
                </div>
            </div>
        </Container>
    )
}
