import React, { useState } from 'react';
import { Form, Button } from 'semantic-ui-react';
import { Alert } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import { Container } from 'react-bootstrap';
import { useAuth } from 'contexts/AuthContexts';

export default function PasswordReset() {
    const { passwordReset } = useAuth();
    
    const initialState = '';
    const [errorMsg, setErrorMsg] = useState(initialState)
    const [successMsg, setSuccessMsg] = useState(initialState)
    const [loading, setLoading] = useState(false)
    const [email, setEmail] = useState(initialState)

    async function handleSubmit(e) {
        e.preventDefault();

        try {
            setErrorMsg('')
            setSuccessMsg('')
            setLoading(true)
            await passwordReset(email)
            setSuccessMsg('Password reset email sent successfully! Please see email for further instructions.')
        } catch (error) {
            setErrorMsg(error.message)
        }

        setLoading(false)
    }

    return (
        <Container className="d-flex align-items-center justify-content-center min-vh-100">
            <div className="w-100 div-main">
                <Form onSubmit={handleSubmit}>
                    <h2 className="text-center mb-4">Login</h2>
                    { errorMsg && <Alert variant='danger'>{errorMsg}</Alert>}
                    { successMsg && <Alert variant='success'>{successMsg}</Alert>}
                    <Form.Field>
                        <label>Email</label>
                        <input type="email" placeholder='jsmith@gmail.com' onChange={(e) => {setEmail(e.target.value)}} required/>
                    </Form.Field>
                    <div className="w-100 text-center mt-2">
                        <Button className="w-50" type='submit' disabled={loading}>Send Email</Button>
                    </div>
                </Form>
                <div className="w-100 text-center mt-2">
                    <Link to="/login">Login</Link>
                </div>
            </div>
        </Container>
    )
}
