import React, { useState } from 'react';
import { Button, Modal, Form, Dropdown } from 'semantic-ui-react';
import { Container, Alert } from 'react-bootstrap'
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';
import { ISSUE_TYPE_OPTIONS, STATUS_OPTIONS } from 'utilities/constants';
import { useAuth } from 'contexts/AuthContexts';


export default function AddItemModal() {
  const { xtAddTask } = useAuth()
  const initialValue = null

  const [open, setOpen] = useState(false)
  const [startDate, setStartDate] = useState(new Date())
  const [dueDate, setDueDate] = useState(new Date())
  const [issueType, setIssueType] = useState(initialValue)
  const [errorMsg, setErrorMsg] = useState(initialValue)
  const [title, setTitle] = useState(initialValue)
  const [patchVersion, setPatchVersion] = useState(initialValue)
  const [category, setCategory] = useState(initialValue)
  const [summary, setSummary] = useState(initialValue)
  const [estimateHours, setEstimateHours] = useState(initialValue)


  function handleSubmit(e) {
    e.preventDefault();
    setErrorMsg('')

    try{
      xtAddTask(
        category,
        estimateHours,
        issueType,
        patchVersion,
        summary,
        title,
        startDate,
        dueDate
      )
    } catch (error) {
      setErrorMsg(error)
    }
    
    setOpen(false)
  }

  function getStatusOptions() {
    let statusOptions = []
    STATUS_OPTIONS.forEach(status => statusOptions.push({key: status, text: status, value: status}))
    return statusOptions
  }

  function getIssueTypeOptions() {
    let issueTypeOptions = []
    ISSUE_TYPE_OPTIONS.forEach(issueType => issueTypeOptions.push({key: issueType, text: issueType, value: issueType}))
    return issueTypeOptions
}

  return (
    <Modal
      className='xt-modal'
      onClose={() => setOpen(false)}
      onOpen={() => setOpen(true)}
      open={open}
      trigger={<Dropdown.Item icon='plus' text='New'></Dropdown.Item>}
    >
      <Container className="d-flex align-items-center justify-content-center mt-4 mb-4">
        <div className='w-100 h-100'>
          <Modal.Content>
            {errorMsg && <Alert variant='danger'>{errorMsg}</Alert>}
            <Form onSubmit={handleSubmit}>
                <div className='xt-add-item-modal-flex' style={{justifyContent: 'space-evenly'}}>
                  <Form.Field>
                    <label>Status</label>
                    <Dropdown
                      fluid
                      selection
                      options={getStatusOptions()}
                      style={{width: '200px'}}
                      value='Open'
                      disabled
                    />
                  </Form.Field>
                  <Form.Field>
                    <label>Issue Type</label>
                    <Dropdown
                      fluid
                      selection
                      options={getIssueTypeOptions()}
                      style={{width: '200px'}}
                      onChange={(e, data) => {setIssueType(data.value)}}
                    />
                  </Form.Field>
                  <Form.Field>
                    <label>Start Date</label>
                    <DatePicker selected={startDate} onChange={date => setStartDate(date)} />
                  </Form.Field>
                  <Form.Field>
                    <label>Due Date</label>
                    <DatePicker selected={dueDate} onChange={date=> setDueDate(date)} />
                  </Form.Field>
                </div>
                <Form.Field>
                    <label>Title</label>
                    <input type="text" onChange={(e) => {setTitle(e.target.value)}} />
                </Form.Field>
                <Form.Field>
                    <label>Summary</label>
                    <input type="text" onChange={(e) => {setSummary(e.target.value)}} />
                </Form.Field>
                <div className='xt-add-item-modal-flex' style={{justifyContent: 'left'}}>
                  <Form.Field>
                      <label>Category</label>
                      <input 
                        type="text" 
                        onChange={(e) => {setCategory(e.target.value)}}
                        style={{width: '200px'}} 
                      />
                  </Form.Field>
                  <Form.Field style={{paddingLeft: '20px'}}>
                      <label>Estimated Hours</label>
                      <input 
                        type="text" 
                        onChange={(e) => {setEstimateHours(e.target.value)}}
                        style={{width: '200px'}}
                      />
                  </Form.Field>
                  <Form.Field style={{paddingLeft: '20px'}}>
                    <label>Patch Version</label>
                    <input 
                      placeholder='1.0.0'
                      type="text" 
                      onChange={(e) => {setPatchVersion(e.target.value)}}
                      style={{width: '200px'}} 
                    />
                  </Form.Field>
                </div>
                <div className="w-100 text-center mt-2">
                </div>
            </Form>
          </Modal.Content>
          <Modal.Actions>
            <Button color='red' onClick={() => setOpen(false)}>
              Cancel
            </Button>
            <Button
              content="Add Item"
              labelPosition='right'
              icon='checkmark'
              onClick={handleSubmit}
              positive
            />
          </Modal.Actions>
        </div>
      </Container>
    </Modal>
  )
}
