import React, { useState } from 'react';
import { Button, Modal, Form, Dropdown, Icon, Segment, Grid } from 'semantic-ui-react';
import { Alert, Container } from 'react-bootstrap'
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';
import { STATUS_OPTIONS, ISSUE_TYPE_OPTIONS } from 'utilities/constants';
import CommentCard from './CommentCard';

export default function ViewItemModal({ modalFields }) {
    const [open, setOpen] = useState(false)
    const { 
        fbDocumentId,
        fbIssueType, 
        fbTitle, 
        fbSummary, 
        fbStatus, 
        fbStartDate,
        fbDueDate, 
        fbEstimateHours, 
        fbActualHours, 
        fbPatchVersion,
        fbCategory,
        fbComments
    } = modalFields;

    function getStatusOptions() {
        let statusOptions = []
        STATUS_OPTIONS.forEach(status => statusOptions.push({key: status, text: status, value: status}))
        return statusOptions
    }

    function getIssueTypeOptions() {
        let issueTypeOptions = []
        ISSUE_TYPE_OPTIONS.forEach(issueType => issueTypeOptions.push({key: issueType, text: issueType, value: issueType}))
        return issueTypeOptions
    }

    return (
        <Modal
        className='xt-modal'
        onClose={() => setOpen(false)}
        onOpen={() => setOpen(true)}
        open={open}
        trigger={<Button primary size='tiny'>View</Button>}
        style={{width: '1400px'}}
        >
            <Segment>
                <Grid columns={2}>
                    <Grid.Column>
                        <Container className="d-flex align-items-center justify-content-center mt-4">
                            <div style={{ width: '700px'}}>
                                <Modal.Content>
                                    <Form>
                                        <div className='xt-add-item-modal-flex' style={{justifyContent: 'space-evenly'}}>
                                        <Form.Field>
                                            <label>Status</label>
                                            <Dropdown
                                            fluid
                                            selection
                                            options={getStatusOptions()}
                                            style={{width: '200px'}}
                                            value={fbStatus}
                                            disabled
                                            />
                                        </Form.Field>
                                        <Form.Field>
                                            <label>Issue Type</label>
                                            <Dropdown
                                            fluid
                                            selection
                                            options={getIssueTypeOptions()}
                                            style={{width: '200px'}}
                                            value={fbIssueType}
                                            disabled
                                            />
                                        </Form.Field>
                                        <Form.Field>
                                            <label>Start Date</label>
                                            <DatePicker selected={fbStartDate} readOnly />
                                        </Form.Field>
                                        <Form.Field>
                                            <label>Due Date</label>
                                            <DatePicker selected={fbDueDate} readOnly/>
                                        </Form.Field>
                                        </div>
                                        <Form.Field>
                                            <label>Title</label>
                                            <input type="text" value={fbTitle} readOnly='true' />
                                        </Form.Field>
                                        <Form.Field>
                                            <label>Summary</label>
                                            <input type="text" value={fbSummary} readOnly='true' />
                                        </Form.Field>
                                        <div className='xt-add-item-modal-flex' style={{justifyContent: 'left'}}>
                                        <Form.Field>
                                            <label>Category</label>
                                            <input 
                                                type="text" 
                                                style={{width: '175px'}}
                                                value={fbCategory}
                                                readOnly
                                            />
                                        </Form.Field>
                                        <Form.Field style={{paddingLeft: '20px'}}>
                                            <label>Estimated Hours</label>
                                            <input 
                                                type="text" 
                                                style={{width: '100px'}}
                                                value={fbEstimateHours}
                                                readOnly
                                            />
                                        </Form.Field>
                                        <Form.Field style={{paddingLeft: '20px'}}>
                                            <label>Actual Hours</label>
                                            <input 
                                                type="text" 
                                                style={{width: '100px'}}
                                                value={fbActualHours}
                                                readOnly
                                            />
                                        </Form.Field>
                                        <Form.Field style={{paddingLeft: '20px'}}>
                                            <label>Patch Version</label>
                                            <input 
                                            placeholder='1.0.0'
                                            type="text" 
                                            style={{width: '200px'}}
                                            value={fbPatchVersion}
                                            readOnly
                                            />
                                        </Form.Field>
                                        </div>
                                        <div className="w-100 text-center mt-2">
                                        </div>
                                    </Form>
                                </Modal.Content>
                                <br />
                                <div className='text-center'>
                                    <Alert variant='danger'><Icon name='warning sign' />Closed tasks cannot be modified<Icon name='warning sign' /></Alert>
                                </div>
                            </div>
                        </Container>
                    </Grid.Column>
                    <Grid.Column>
                        <CommentCard 
                        docId={fbDocumentId}
                        comments={fbComments}
                        status={fbStatus}
                        />
                    </Grid.Column>
                </Grid>
            </Segment>
        </Modal>
    )
}
