import React, { useContext, useState, useEffect } from 'react';
import { auth, xtFirestore } from '../firebase';
import firebase from 'firebase';
import { FIRESTORE_TASK_COLLECTION, FIRESTORE_COMMENT_COLLECTION } from '../utilities/constants';

const AuthContext = React.createContext();

export function useAuth() {
    return useContext(AuthContext)
}

export function AuthProvider({ children }) {
    const [currentUser, setCurrentUser] = useState()
    const [loading, setLoading] = useState(true)

    // Functions to run with authenticated user
    function signup(email, password) {
        return auth.createUserWithEmailAndPassword(email, password)
    }

    function login(email, password) {
        return auth.signInWithEmailAndPassword(email, password)
    }

    function logout() {
        return auth.signOut()
    }

    function updateEmail(email){
        return currentUser.updateEmail(email)
    }

    function updatePassword(password) {
        return currentUser.updatePassword(password)
    }

    function updateDisplayName(name) {
        return currentUser.updateProfile({
            displayName: name
        })
    } 

    function passwordReset(email){
        return auth.sendPasswordResetEmail(email)
    }

    function xtAddTask(
        category,
        estimateHours,
        issueType,
        patchVersion,
        summary,
        title,
        startDate,
        dueDate
    ) {
        return xtFirestore
            .collection(FIRESTORE_TASK_COLLECTION)
            .add({
                category: category,
                estimateHours: estimateHours,
                issueType: issueType,
                patchVersion: patchVersion,
                status: 'Open',
                summary: summary,
                title: title,
                startDate: startDate,
                dueDate: dueDate,
                createdDate: firebase.firestore.FieldValue.serverTimestamp()
            })
    }
    
    function xtAddComment(
        taskId,
        text,
        userEmail,
        userName
    ) {
        return xtFirestore
            .collection(FIRESTORE_COMMENT_COLLECTION)
            .add({
                taskId: taskId,
                text: text,
                userEmail: userEmail,
                userName: userName,
                addedDate: firebase.firestore.FieldValue.serverTimestamp()
            })
    }

    function xtUpdateTask(
        docId,
        category,
        estimateHours,
        actualHours,
        issueType,
        patchVersion,
        status,
        summary,
        title,
        startDate,
        dueDate
    ) {
        return xtFirestore
            .collection(FIRESTORE_TASK_COLLECTION)
            .doc(docId)
            .update({
                category: category,
                estimateHours: estimateHours,
                actualHours: actualHours,
                issueType: issueType,
                patchVersion: patchVersion,
                status: status,
                summary: summary,
                title: title,
                startDate: startDate,
                dueDate: dueDate,
            })
    }

    useEffect(() => {
        const unsubscribe = auth.onAuthStateChanged(user => {
            setCurrentUser(user)
            setLoading(false)
        })
        return unsubscribe
    }, [])

    const value = {
        //Place functions here to use 
        currentUser,
        signup,
        login,
        logout,
        updateEmail,
        updatePassword,
        updateDisplayName,
        passwordReset,
        xtAddTask,
        xtUpdateTask,
        xtAddComment
    }

    return (
        <AuthContext.Provider value={value}>
            { !loading && children }
        </AuthContext.Provider>
    )
}
